.profile__user-name {
  margin: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.14;
  color: #fff;

  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media (max-width: 524px) {
  .profile__user-name {
    font-size: 27px;
    line-height: 1.21;
  }
}
