.profile__avatar-overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 120px;
  height: 120px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #000;
  opacity: 0.8;

  visibility: hidden;
}

.profile__avatar-overlay:hover {
  cursor: pointer;
}
